import React, { useContext } from 'react';
import { graphql, useStaticQuery } from 'gatsby';
import styled from '@emotion/styled';

import HeaderSection from '@components/HeaderSection';
import Header_bg from '../../assets/Map_blurlinear_1.png';
import Bio from '@components/Bio';
import Icons from '@icons';
import mediaqueries from '@styles/media';
import { IAuthor } from '@types';

const headerQuery = graphql`
  {
    site: allSite {
      edges {
        node {
          siteMetadata {
            hero {
              heading
              heading_2
              maxWidth
              backgroundImage
              backgroundPosition
              backgroundRepeat
            }
          }
        }
      }
    }
  }
`;

function HeadingHero() {

  const results = useStaticQuery(headerQuery);
  const hero = results.site.edges[0].node.siteMetadata.hero;

  return (
    <HeaderSection relative id="Headers__Hero">
      <HeaderContainerGrid>
        <HeadingContainer 
        style={{ 
          maxWidth: `${hero.maxWidth}px`, 
          }}
        >
          <HeroHeading>
          Untuk Indonesia <span style={{color: "#e40c2b"}}>setara</span> di bidang teknologi!
          </HeroHeading>
          <HeadingDescriptionContainer>
            <HeroDescription dangerouslySetInnerHTML={{ __html: hero.heading_2 }} />
            <IconContainer>
              <IconHeader>
                <a href='https://www.instagram.com/pemimpin.indonesia/'>
                  <Icons.Instagram fill='#73737D'/>
                </a>
              </IconHeader>
              <IconHeader>
                <a href='https://www.instagram.com/pemimpin.indonesia/'>
                  <Icons.Spotify fill='#73737D'/>
                </a>
              </IconHeader>
            </IconContainer>
          </HeadingDescriptionContainer>
        </HeadingContainer>
        <ImageHeaderContainer>
          <ImageContainer>
            <img src={Header_bg} style={{ width:'auto', height:'360px', alignSelf:'center' }}/>
          </ImageContainer>
        </ImageHeaderContainer>
      </HeaderContainerGrid>
    </HeaderSection>
  );
}

export default HeadingHero;

const HeaderContainerGrid = styled.div`
  width: 100%;
  text-align: left;
  padding-top: 0px;
  display: grid;
  display: -ms-grid;
  grid-template-rows: 1fr;
  grid-template-columns: 1.6fr 0.4fr;
  grid-template-areas: "HeadingContainer" "ImageHeaderContainer";
  grid-area: HeaderContainerGrid;
  align-items: left;
  justify-content: space-evenly;
  color: #111216;
  align-self: center;
  margin-left: 5vh;
  margin-right: 5vh;
`;

const HeadingContainer = styled.div`
  grid-area: "HeadingContainer"  
  display: flex;
  flex-flow: row;
  -ms-flex-flow: row;
  padding: 0vh;
  align-self: center;
  margin: 2vh 0;
  height: 60vh;
  z-index: 10;

  ${mediaqueries.desktop`
  width: 100%;
  `}

  ${mediaqueries.tablet`
  width: 100%;
  `}
`;

const HeroHeading = styled.h1`
  z-index: 10;
  margin-top: 16vh;
  font-style: normal;
  font-weight: 600;
  font-size: 52px;
  line-height: 1.15;
  color: ${p => p.theme.colors.primary};

  a {
    color: ${p => p.theme.colors.accent};
  }

  ${mediaqueries.desktop`
    font-size: 38px
  `}

  ${mediaqueries.phablet`
    font-size: 32px;
  `}
`;

const HeadingDescriptionContainer = styled.div`
  display: flex;
  align-items: center;
  margin: 10px 0;

  ${mediaqueries.desktop`
    width: 60%;
  `}

  ${mediaqueries.tablet`
    width: 100%;
  `}
`;

const HeroDescription = styled.h3`
  font-style: normal;
  font-weight: 300;
  font-size: 32px;
  line-height: 1.15;
  color: ${p => p.theme.colors.primary};

  a {
    color: ${p => p.theme.colors.accent};
  }

  ${mediaqueries.desktop`
    font-size: 24px
  `}

  ${mediaqueries.phablet`
    font-size: 18px;
  `}
`;

const ImageHeaderContainer = styled.div`
  grid-area: "ImageHeaderContainer"  
  display: flex;
  flex-flow: row;
  -ms-flex-flow: row;
  padding: 0vh;
  align-self: center;
  height: 60vh;
  z-index: 0;

  ${mediaqueries.desktop`
    width: 50%;
  `}

  ${mediaqueries.tablet`
    display: none;
  `}
`;

const ImageContainer = styled.div`
  z-index: 0;
  width: auto !important;
  max-height: 100% !important;
  padding: 1vh;
  align-self: center;
  top: 5%;
  right: 15%;
  position: absolute;
`;

const IconContainer = styled.div`
  display: flex;
  position: relative;
  align-items: center;
  justify-content: flex-start;
  flex-grow: column;
`;

const IconHeader = styled.div`
  margin-left: 15px;
  padding-top: 5px;
  padding-bottom: 0px;
  align-items: center;
`;




